import { createRouter, createWebHistory } from 'vue-router'
import('../views/SubmitedView.vue')


 /* @vite-ignore */

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes: [
    {
      path: '/user',
      name: 'UserView',
      component: () => import('../views/UserView.vue')
    },
    {
      path: '/simple',
      name: 'simple',
      component: () => import('../views/SimpleFromView.vue')
    },
    {
      path: '/submited',
      name: 'submited',
      component: () => import('../views/SubmitedView.vue'),
    },
    {
      path: '/select',
      name: 'select',
      component: () => import('../views/SelectView.vue'),
    },
    {
      path: '/',
      name: 'index',
      component: () => import('../views/UserView.vue'),
    }

    

  ]
})

export default router
