const ajv = new Ajv();

export type settingT = {
  dayofweek: number;
  maxday: number;
  minday: number;
  quota: number;
  specialAge: number;
  specialQuota: number;
};

const settingSchema: JSONSchemaType<settingT> = {
  type: "object",
  properties: {
    dayofweek: {type: "integer"},
    maxday: {type: "integer"},
    minday: {type: "integer"},
    quota: {type: "integer"},
    specialAge: {type: "integer"},
    specialQuota: {type: "integer"},
  },
  required: ["dayofweek", "maxday", "minday", "quota", "specialAge", "specialQuota"],
  additionalProperties: false,
};

const setting: settingT = {
  dayofweek: 3,
  maxday: 21,
  minday: 7,
  quota: 6,
  specialAge: 3,
  specialQuota: 3,
};

export function getSetting() {
  return new Promise<settingT>((resolve, reject) => {
    resolve(setting);
  })
}


